.top-menu {
  .navbar-brand {
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      width: 50px;
      height: 50px;
      margin-right: 20px;
    }

    h2 {
      margin: 0;
      font-size: 22px;
    }
  }

  @media all and (max-width: 768px) {
    .navbar-brand {
      h2 {
        font-size: 18px;
      }
    }
  }
}